const container = document.getElementById('root')
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json'
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const store = {
	currentPage: 1
}
function getData(url) {
	ajax.open('GET', url, false) // Îç∞Ïù¥ÌÑ∞Î•º ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Í∞ÄÏ†∏Ïò§Í≤†Îã§.
	ajax.send(); // Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò®Îã§.

	return JSON.parse(ajax.response);
}

function newsFeed() {
	const newsFeed = getData(NEWS_URL);
	const newsList = [];
	const maxPage = newsFeed.length%10 === 0 ? newsFeed.length/10 : newsFeed.length/10+1;

	let template = `
		<div class="container mx-auto p-4">
			<h1>Hacker News</h1>
			<ul>
				{{__news_feed__}}
			</ul>
			<div>
				<a href="#/page/{{__prev_page__}}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
				<a href="#/page/{{__next_page__}}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
			</div>
		</div>
	`

	for(let i = (store.currentPage -1) * 10; i < store.currentPage*10; ++i) {
		newsList.push(`
			<li>
				<a href='#/show/${newsFeed[i].id}'>
					${newsFeed[i].title} (${newsFeed[i].comments_count})
				</a>
			</li>
		`)
	};
	template = template.replace('{{__news_feed__}}', newsList.join(''))
	template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage-1 : 1)
	template = template.replace('{{__next_page__}}', store.currentPage !== maxPage ? store.currentPage+1 : maxPage)
	container.innerHTML = template;
}

function newsDetail() {
	// console.log('Ìï¥ÏãúÍ∞Ä Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.')
	// console.log(this.location) url Ï£ºÏÜåÏóê ÎåÄÌïú Ï†ïÎ≥¥Î°ú hashÏóê ÎåÄÌïú Í∞íÏùÑ Ï∂îÏ∂ú Í∞ÄÎä•.
	const id = location.hash.substr(7) // #ÏúºÎ°ú ÏãúÏûëÌïòÎäî Ìï¥Ïâ¨Í∞íÏóêÏÑú #ÏùÑ Ï†úÍ±∞ÌïòÏó¨ id Í∞íÎßåÏùÑ ÏàúÏàòÌïòÍ≤å Ï∂îÏ∂úÌïòÏó¨ Ï†ÄÏû•
	const newsContent = getData(CONTENT_URL.replace('@id', id));

	container.innerHTML = `<h1>${newsContent.title}</h1><div><a href='#/page/${store.currentPage}'>Î™©Î°ùÏúºÎ°ú</div>`;
}


// Ìï¥Ïâ¨Í∞íÏóê Îî∞Îùº Ïñ¥Îñ§ Ìï®ÏàòÎ•º Ïã§ÌñâÌïòÏó¨ ÏÇ¨Ïö©ÏûêÏóêÍ≤å Ï†úÍ≥µÌï† Í≤ÉÏù∏ÏßÄÎ¶¨Î•º ÌåêÎã®ÌïòÎäî Ìï®Ïàò.
// ÌôîÎ©¥ Ï†ÑÌôòÏùò Ï£ºÏöî ÏüÅÏ†ê
function router() {
	const routePath = location.hash;
	// console.log(routePath); // #/page/(number)

	if (routePath === '') {
		newsFeed();
	} else if(routePath.indexOf('#/page/') >= 0){
		store.currentPage = +routePath.substr(7);
		newsFeed();
	} else {
		newsDetail();
	}
}

window.addEventListener('hashchange', router);

router();